.slide__after_md { padding-left: 60px; width: 320px; }

@media only screen and (max-width: 1200px) { .slide__before, .slide__after { position: relative; z-index: 2; padding: 0; width: 100%; color: #000; } }

.slide__bg { position: relative; *zoom: 1; }

.slide__bg:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 1200px) { .slide__bg { padding-bottom: 40px; } }

.slide__bg-in { *zoom: 1; }

.slide__bg-in:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 1200px) { .slide__bg-in { padding: 53px 50px 30px 63px; background: #ebbf00; } }

@media only screen and (max-width: 600px) { .slide__bg-in { padding: 40px; } }

.slide__footer { position: absolute; bottom: 0; left: 0; right: 0; padding: 0 15px; height: 42px; font-size: 0; display: none; }

@media only screen and (max-width: 1200px) { .slide__footer { display: block; } }

.slide__footer:before { content: ""; position: absolute; left: 0; right: 0; top: 0; bottom: 0; background: #ebbf00; transform: skewY(-2deg); transform-origin: left bottom; }

.slide__footer i { position: relative; z-index: 1; display: inline-block; vertical-align: top; margin: 25px 10px 0 0; border-radius: 50%; width: 20px; height: 20px; background: #181818; }

.slide__decor { position: absolute; top: 0; left: 200px; bottom: 8px; border-right: 4px solid #ebbf00; }

@media only screen and (max-width: 1200px) { .slide__decor { position: static; padding-left: 35px; margin-bottom: 60px; border: none; } }

@media only screen and (max-width: 600px) { .slide__decor { margin-bottom: 35px; } }

.slide__decor_center { left: 290px; }

.slide__decor:before, .slide__decor:after { content: ""; position: absolute; top: 86px; left: 0; }

@media only screen and (max-width: 1200px) { .slide__decor:before, .slide__decor:after { display: none; } }

.slide__decor:after { margin-top: 7px; width: 0px; height: 0px; border-style: solid; border-width: 16.5px 0 16.5px 11px; border-color: transparent transparent transparent #000; }

.slide__decor:before { width: 0px; height: 0px; border-style: solid; border-width: 23.5px 0 23.5px 16px; border-color: transparent transparent transparent #ebbf00; }

.slide__decor .decor { display: none; }

@media only screen and (max-width: 1200px) { .slide__decor .decor { display: block; } }

.benefits { position: relative; padding: 120px 0 110px; margin-top: 100px; color: #fff; }

@media only screen and (max-width: 1200px) { .benefits { padding: 70px 0 0; margin-top: 80px; } }

.benefits__bg { position: absolute; top: 0; left: 0; right: 0; bottom: 0; background-repeat: no-repeat; background-position: center; background-size: cover; }

@media only screen and (max-width: 1200px) { .benefits__bg { background-position: 41% -150px; background-size: auto 1300px; } }

@media only screen and (max-width: 600px) { .benefits__bg { background-position: 30% -50px; background-size: auto 500px; } }

.benefits__title { font-family: "PFDinPro Light", Arial, sans-serif; font-size: 42px; line-height: 1.05; }

@media only screen and (max-width: 1200px) { .benefits__title { max-width: 370px; font-size: 50px; line-height: 1.04; } }

@media only screen and (max-width: 600px) { .benefits__title { padding: 0 20px; max-width: 320px; font-size: 32px; line-height: 1.13; } }

.benefits__title em { display: block; color: #ebbf00; font-family: "PFDinPro Medium", Arial, sans-serif; }

.benefits__list { position: relative; margin: 0 -18px; font-size: 0; }

@media only screen and (max-width: 1200px) { .benefits__list { padding: 70px 0 95px; } }

@media only screen and (max-width: 600px) { .benefits__list { padding: 50px 0; margin: 0; } }

.benefits__list:before { content: ""; position: absolute; top: 0; bottom: 0; left: 50%; width: 100vw; background: #000; transform: translate3d(-50%, 0, 0); display: none; }

@media only screen and (max-width: 1200px) { .benefits__list:before { display: block; } }

.benefits__item { position: relative; z-index: 2; display: inline-block; vertical-align: top; padding: 0 18px; width: 20%; }

@media only screen and (max-width: 1200px) { .benefits__item { display: block; margin-bottom: 50px; width: 100%; } }

@media only screen and (max-width: 600px) { .benefits__item { padding: 0 20px; margin-bottom: 30px; } }

.benefits__icon { height: 80px; text-align: center; font-size: 0; text-align: left; margin-bottom: 20px; }

.benefits__icon:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

@media only screen and (max-width: 1200px) { .benefits__icon { height: auto; margin-bottom: 30px; } }

.benefits__icon .icon { display: inline-block; vertical-align: middle; }

.benefits__icon .icon-connect { font-size: 55px; }

