@media only screen and (max-width: 1200px) { .benefits__icon .icon { font-size: 128px; } }

@media only screen and (max-width: 600px) { .benefits__icon .icon { font-size: 72px; } }

.benefits__text { font-family: "PFDinPro Light", Arial, sans-serif; font-size: 20px; line-height: 1.11; }

@media only screen and (max-width: 1200px) { .benefits__text { font-family: "PFDinPro Light", Arial, sans-serif; font-size: 38px; line-height: 1.08; } }

@media only screen and (max-width: 600px) { .benefits__text { font-size: 18px; line-height: 1.23; } }

.benefits .icon-title-arr { display: block; margin: 20px 0 45px; fill: #ebbf00; font-size: 17px; }

@media only screen and (max-width: 1200px) { .benefits .icon-title-arr { font-size: 28px; margin-bottom: 145px; } }

@media only screen and (max-width: 600px) { .benefits .icon-title-arr { margin-bottom: 80px; } }

